@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Roboto+Condensed:wght@400;700&family=Roboto:wght@400;500&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  @media only screen  and (max-width: 768px) { display: none;}
  background-color: rgba(49, 49, 49, 1);
  width: 100%;
  height: 38px;
  display: flex;

  .navigation {
    display: flex;
    align-items: center;
    gap: 30px;

    &:nth-child(1) {
        margin-left: 140px;
    }

    a {
      text-decoration: none;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      color: white;
    }
  }

  .contacts {
    display: flex;
    margin-left: auto;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-size: 14px;
    color: white;
    
    .contacts_tel {
      display: flex;
      align-items: center;
      .tel-img{
        margin-right: 10px;
      }
    }

    .Deliveri-link {
      display: flex;
      align-items: center;
      margin-left: 20px;
      .car-img {
        margin-right: 10px;
      }
      a {
        text-decoration: none;
        font-family: 'Roboto', sans-serif;
        font-size: 14px;
        font-weight: 400;
        color: white;
        margin-right: 120px;
      }
    }
  }
}

.page-margins{
  margin-right: 120px;
  margin-left: 140px;

  @media only screen  and (max-width: 815px) {
    margin-right: 23px;
    margin-left: 25px;
  }
  @media only screen  and (max-width: 480px) {
    margin-right: 0px;
    margin-left: 0px;
    padding-left: 0;
  }

  .main-section {
    .searching-string {
      display: flex;
      align-items: center;
      font-family: 'Roboto', sans-serif;
      font-size: 14px;
      font-weight: 400;
      color: white;
      height: 90px;

      .menu-icon{
        display: none;

        @media only screen  and (max-width: 768px) {
          display: block;
          margin-left: 5px;
          margin-right: 5px;
        }
      }
      .Deliveri-link-tablet{
        display: none;

        @media only screen  and (max-width: 768px) { 
          display:flex;
          .car-img {
            margin-right: 5px;
            filter: invert(100%);
          }
          a {
            text-decoration: none;
            font-family: 'Roboto', sans-serif;
            font-size: 14px;
            font-weight: 400;
            color: black;
            margin-right: 120px;

            @media only screen  and (max-width: 768px) {
              margin-right: 5px;
            }
          }
        }
      }
      .logo {
        padding-right: 38px;

        @media only screen  and (max-width: 1200px) {
          padding-right: 0px;
        }
      }
      .searching-string-right-icons {
        display: flex;
        margin-left: auto;
        
        .bag {
          margin-left: 15px;
          margin-right: 30px;

          @media only screen  and (max-width: 1200px) {
            margin-left: 5px;
            margin-right: 10px;
          }
        }
      }
      
      input {
        background: url(/img/search-icon.png)  no-repeat  left  ;
        background-position-x: 15px;
        padding-left: 40px;
        width: 72%;
        height: 50px;

        @media only screen  and (max-width: 480px){
          display: none;
        }
      }
    }

    .navigation {

      a{
        text-decoration: none;
        font-family: 'Roboto', sans-serif;
        font-size: 16px;
        font-weight: 400;
        color: black;
      }
      @media only screen  and (max-width: 1200px) {
        display: none;
        width: auto;
      }
      margin-bottom: 30px;
      display: flex;
      justify-content:space-between ;
      height: 70px;
      background: #FFFFFF;
      box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.07);

      :nth-child(n) {
      display: flex;
      align-items: center;
      }
      img:nth-of-type(n) {
        margin-right: 10px;
      }
      .kitchen-icon{
        margin-left: 40px;
      }
      .etc-button {
        border-style: none;
        background-color: white;
        width: 5px;
      }
    }
    .navigation_tablet{
      a{
        text-decoration: none;
        font-family: 'Roboto', sans-serif;
        font-size: 16px;
        font-weight: 400;
        color: black;
      }
      display: none;
      @media only screen  and (max-width: 1200px) {
        display: flex;
        justify-content: center;
        justify-content: space-between;
        margin-bottom: 30px;
        margin-left: 10px;
        margin-right: 8px;
        gap: 10px;
        height: 40px;
        background: #FFFFFF;
        box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.07);
        font-family: 'Roboto', sans-serif;
        font-size: 16px;
        font-weight: 400;
        @media only screen  and (max-width: 480px){
          gap: 5px;
        }
      }
    }
    .ad {
      display: flex;
      background: url(/img/loft.png)  center ;
      background-size: cover;
      height: 450px;
      align-items: center;
      margin-bottom: 30px;
    }
    .ad_left-button_tablet{
      display: none;
      @media only screen  and (max-width: 768px){
        display: flex;
        margin-left: auto;
        margin-right: 35px;
          .ad_left-button_div{
            display: flex;
            height: 30px;
            width: 30px;
            justify-content: center;
            align-items: center;
            background-color: rgba(255, 255, 255, 1);
            .img-stroke {
              filter: invert(30%);
            }
          }     
        }
        @media only screen  and (max-width: 480px){
          display: none;
        }
      }
    .ad_left-button{
      @media only screen  and (max-width: 768px){
        display:none;
      }
      margin-left: 37px;
      height: 30px;
      display: flex;
      align-items: center;
      .ad_left-button_div{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        background-color: rgba(157, 157, 157, 0.28);
        }
      }

    .ad_main-info{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-left: 65px;

      .ad_main-info_head {
        font-family: 'Playfair Display', serif;
        font-size: 50px;
        font-weight: 700;
        text-transform: uppercase;
        margin-bottom: 15px;
      }
      .ad_main-info_text {
        font-family: 'Roboto Condensed', sans-serif;
        font-size: 16px;
        font-weight: 400;
        margin-bottom: 20px;
      }
      .ad_main-info_button{
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 12px;
        font-weight: 700;
        width: 163px;
        height: 45px;
        background-color:  white;
      }
    }

    .ad_right-button{
      margin-left: auto;
      margin-right: 35px;

      @media only screen  and (max-width: 480px){
        display: none;
      }

      .ad_right-button_div{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 30px;
        height: 30px;
        background-color: rgba(255, 255, 255, 1);
        transform:rotate(180deg);
        .img-stroke {
          filter: invert(30%);
        }
      }
    }
  }

  .category-section {

    .opened-list {
      font-family: 'Roboto', sans-serif;
      display: flex;
      font-weight: 400;
      font-size: 16px;
      text-decoration: none;
      color: black;
      margin-bottom: 20px;

      @media only screen  and (max-width: 768px){
        margin-left: 20px;
      }
    }
    .products{
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;

      @media only screen  and (max-width: 615px){
        justify-content: center;
      }
      .product {
        position: relative;
        width: 263px;
        display: flex;
        flex-direction: column;
        margin-left: 15px;
        .main-info-product-contetnt{
          .product_heart{
            display: block;
            margin-top: 35px;
            margin-right: 16px;
            margin-left: auto;
          }
          .product_image{
            margin-left: 12%;
          }
          .name {
            margin-top: 50px;
            font-family: 'Roboto', sans-serif;
            font-weight: 600;
            font-size: 16px;
            margin-left: 15px;
            ;
          }
          .price {
            font-family: 'Roboto', sans-serif;
            font-weight: 600;
            font-size: 16px;
            margin-left: 15px;
          }
          .category {
            font-family: 'Roboto', sans-serif;
            font-weight: 400;
            font-size: 11px;
            margin-left: 15px;
          }
        }
      
        
        .dropdown-content {
          display: none;
          position: absolute;
          background-color: #f9f9f9;
          width: 263px;
          box-shadow: 0px 8px 16px 8px rgba(0,0,0,0.2);
          z-index: 1;
          .position-none{
            width: 263px;
            display: flex;
            
            flex-direction: column;
            background-color: white;
            .sizes{
              display: flex;
              align-items:center ;
              margin-left: 15px;
              gap: 15px;
            }
            .button-conteiner{
              display: flex;
              justify-content: center;
              .button_add-to-bye{
              width: 223px;
              height: 40px;
              margin-top: 17px;
              margin-bottom: 20px;
              background-color: rgba(36, 84, 98, 1);
              color: white;
              }
            }
            
          }
          .size {
            font-family: 'Roboto', sans-serif;
            font-weight: 400;
            font-size: 12px;
            margin-bottom: 8px;
            margin-top: 20px;
            margin-left: 15px;
          }
          .sizes {
            font-family: 'Roboto', sans-serif;
            font-weight: 400;
            font-size: 9px;
          }
          .grey-sizes{
            color: rgba(196, 196, 196, 1);
            margin-bottom: 8px;
          }
        }
      }
      .product:hover {
          .dropdown-content {
            display: block;
          }
        }  
    }
  }
  .footer{
    display: flex;
      @media only screen  and (max-width: 600px){
        display: none;
      }
    .footer_navigation{
      margin-top: 60px;
      .footer_navigation_main{
        font-family: 'Roboto', sans-serif;
        font-weight: 500;
        font-size: 14px;
        margin-bottom: 30px;
      }
      .footer_navigation_classes{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        width: 384px;
        height: 60px;
        
        a {
          font-family: 'Roboto', sans-serif;
          font-weight: 400;
          font-size: 14px;
          text-decoration: none;
          color: black;
        }
      }
      .footer_navigation_sales{
        display: flex;
        margin-bottom: 40px;
        align-items: flex-start;

        @media only screen  and (max-width: 1100px){
          display: none;
        }
        
        a{
          font-family: 'Roboto', sans-serif;
          font-weight: 400;
          font-size: 14px;
          text-decoration: none;
          color: black;
          margin-right: 50px;
          margin-top: 30px;
        }
        .sale-link{
          color: red;
        }
      }
    }
    .footer_contacts{
      display: flex;
      flex-direction: column;
      justify-content: flex-end;
      align-items: flex-end;
      gap: 30px;
      margin-left: auto;
  
      .footer_contacts_logo{
        font-family: 'Roboto', sans-serif;
        font-weight: 900;
        font-size: 42px;
      }
      .footer_contacts_adress{
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        font-size: 14px;
      }
      .footer_contacts_link{
        display: flex;
        gap: 30px;
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        font-size: 12px;
        margin-bottom: 40px;

        @media only screen  and (max-width: 1100px){
          display: none;
        }

        .footer_contacts_link_tel-block{
          display: flex;
          justify-content: center;
          align-content: center;
        }
        .footer_contacts_link_inst-block{
          display: flex;
          justify-content: center;
          align-content: center;
          a{
            font-family: 'Roboto', sans-serif;
            font-weight: 400;
            font-size: 14px;
            text-decoration: none;
            color: black;
          }
        }
        .footer_contacts_link_mail-block{
          display: flex;
          justify-content: center;
          align-content: center;
        }
      }
    }
  }
  .footer_contacts_link_tablet{
    display: none;

    @media only screen  and (max-width: 825px){
      display: flex;
      flex-direction: column;
    }
    @media only screen  and (max-width: 1100px){
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
      margin-bottom: 20px;
      gap: 10px;
      
      a{
        font-family: 'Roboto', sans-serif;
        font-weight: 400;
        font-size: 14px;
        text-decoration: none;
        color: black;
      }

      @media only screen  and (max-width: 600px){
        display: none;
      }
    }
  }

}
